{
    display: grid;
    align-content: center;
    height:100vh;
    width:100vw;
    margin:0;
}

.battleship-container
{
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    width: 100%;
    height: 100%;
}


.board-container
{
    display: grid;
    grid-template-rows: 1fr 9fr;
    align-content: center;
    width: 100%;
    height: 100%
}

.board
{
    background-color: navy;
    height:100%;
    aspect-ratio: 1 / 1;
    min-width: 300px;
    min-height: 300px;
    justify-self: center;
    border-radius: 16px;
    padding:4px;
    display:grid;  
    grid-template-columns: repeat(9 , 1fr);
    grid-template-rows: repeat(9,1fr);

}

.tile
{
    border-radius: 8px;
    border: 2px white solid;
    background-color: transparent;
}

.tile:hover
{
    cursor: pointer;
}

.empty-tile
{
    /*Normal Tile*/
}

.miss
{
    background-color: white;
}

.hit
{
    background-color: red;
}

.sunk
{
    background-color: purple;
}

.ship-exists
{
    background-color: orange;
}`, "",{"version":3,"sources":["webpack://./src/css/styles.css"],"names":[],"mappings":"AAAA;;IAEI,aAAa;IACb,qBAAqB;IACrB,YAAY;IACZ,WAAW;IACX,QAAQ;AACZ;;AAEA;;IAEI,aAAa;IACb,kCAAkC;IAClC,WAAW;IACX,YAAY;AAChB;;;AAGA;;IAEI,aAAa;IACb,2BAA2B;IAC3B,qBAAqB;IACrB,WAAW;IACX;AACJ;;AAEA;;IAEI,sBAAsB;IACtB,WAAW;IACX,mBAAmB;IACnB,gBAAgB;IAChB,iBAAiB;IACjB,oBAAoB;IACpB,mBAAmB;IACnB,WAAW;IACX,YAAY;IACZ,sCAAsC;IACtC,iCAAiC;;AAErC;;AAEA;;IAEI,kBAAkB;IAClB,uBAAuB;IACvB,6BAA6B;AACjC;;AAEA;;IAEI,eAAe;AACnB;;AAEA;;IAEI,cAAc;AAClB;;AAEA;;IAEI,uBAAuB;AAC3B;;AAEA;;IAEI,qBAAqB;AACzB;;AAEA;;IAEI,wBAAwB;AAC5B;;AAEA;;IAEI,wBAAwB;AAC5B","sourcesContent":["body\n{\n    display: grid;\n    align-content: center;\n    height:100vh;\n    width:100vw;\n    margin:0;\n}\n\n.battleship-container\n{\n    display: grid;\n    grid-template-columns: 1fr 2fr 1fr;\n    width: 100%;\n    height: 100%;\n}\n\n\n.board-container\n{\n    display: grid;\n    grid-template-rows: 1fr 9fr;\n    align-content: center;\n    width: 100%;\n    height: 100%\n}\n\n.board\n{\n    background-color: navy;\n    height:100%;\n    aspect-ratio: 1 / 1;\n    min-width: 300px;\n    min-height: 300px;\n    justify-self: center;\n    border-radius: 16px;\n    padding:4px;\n    display:grid;  \n    grid-template-columns: repeat(9 , 1fr);\n    grid-template-rows: repeat(9,1fr);\n\n}\n\n.tile\n{\n    border-radius: 8px;\n    border: 2px white solid;\n    background-color: transparent;\n}\n\n.tile:hover\n{\n    cursor: pointer;\n}\n\n.empty-tile\n{\n    /*Normal Tile*/\n}\n\n.miss\n{\n    background-color: white;\n}\n\n.hit\n{\n    background-color: red;\n}\n\n.sunk\n{\n    background-color: purple;\n}\n\n.ship-exists\n{\n    background-color: orange;\n}"],"sourceRoot":""}]);
